.AllBlogs{
    padding: 0;
    margin: 0;
    .wrapper1{        
        .col-md-12{
            padding: 100px 250px;
            text-align: center;
            @media (max-width:1400px){
                padding: 70px 150px;
            }
            @media (max-width:1000px){
                padding: 30px 100px;
            }
            @media (max-width:800px){
                padding: 30px 50px;
            }
            @media (max-width:500px){
                padding: 0px 50px 30px 50px;               
            }
            h1{
                margin-top: 200px;
                font-size: 68px;
                text-align: left;
                padding: 20px 70px;
                font-family: 'Oswald', sans-serif;
                font-weight: 600;
                @media (max-width:1400px){
                    padding: 20px 50px;
                    font-size: 60px;
                }
                @media (max-width:1200px){                  
                    font-size: 55px;
                }
                @media (max-width:900px){                  
                    font-size: 50px;
                    padding: 20px 30px;
                }
                @media (max-width:500px){
                    padding: 20px 0;
                    font-size: 50px;
                }
                @media (max-width:430px){
                    padding: 20px 0;
                    font-size: 42px;
                }
                @media (max-width:380px){
                    padding: 20px 0;
                    font-size: 35px;
                }
            }
            img{
                margin-top: 50px;
                min-width: 90%;
                max-height: 50vh;
                @media (max-width:500px){
                    max-width: 100%;
                }
            }
            .date{                
                text-align: left;
                padding: 0 70px 0px 70px;
                @media (max-width:1400px){
                    padding: 0 50px 0px 50px;
                }
                @media (max-width:900px){
                    padding: 0 30px;
                }
                @media (max-width:500px){
                    padding: 20px 0;
                    font-size: 14px;
                }
                @media (max-width:460px){
                    padding: 10px 0;
                    font-size: 12px;
                }
                @media (max-width:400px){
                    padding: 5px 0;
                    font-size: 10px;
                }
            } 
            .text{
                text-align: left;
                padding: 0px 70px;
                @media (max-width:1400px){
                    padding: 0 50px;
                }
                @media (max-width:900px){
                    padding: 0 30px;
                }
                @media (max-width:500px){
                    padding: 0;
                }
            }                       
        }
    }   
    .header-images{
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        overflow: hidden;
        .flower{
            // border: solid 1px;
            position: absolute;
            left: -15%;
            overflow: hidden;  
            z-index: -1;          
            @media (max-width:767px){
                display: none;
            }
            img{
                width: 100%;
                height: 100%;
                animation-name: animflower;
                animation-duration: 30s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
            }
        }
        .two-flower{
            // border: solid 1px;
            position: absolute;
            right: -5%;
            overflow: hidden;  
            z-index: -1;    
            // height: 70%;
            // width: 25%;
            transform: scaleY(1.3) rotate(4deg);      
            @media (max-width:767px){
                display: none;
            }
            img{
                width: 100%;
                height: 100%;
                animation-name: animtwoflower;
                animation-duration: 25s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
            }
        }
        .bg-creem{
            padding: 0;
            margin: 0;
            position: absolute;
            right: 0;
            top: 0;
            width: 30%;
            height: 100%;
            z-index: -1;
            background-color: #F9F4EA;
            @media (max-width:767px){
                display: none;
            }
        }
    } 
}