.Footer{
    width: 100%;
    .wrapper{
        padding: 50px 200px;
        @media (max-width:1510px){
            padding: 50px 100px;
        }
        @media (max-width:1310px){
            padding: 50px 50px;
        }
        @media (max-width:1140px){
            padding: 50px 30px;
        }        
        .f1{                       
            .col-md-12{
                padding: 30px 30px 0 30px;
                .row{                                        
                    justify-content: space-between;
                    padding-top: 40px;
                    align-items: center;
                }
                .col-md-4{
                    h6{
                        color: black;
                        font-weight: 400;
                        @media (max-width:500px){
                            display: none;
                        }
                    }
                }
                .col-md-5{
                    display: flex;                   
                    justify-content: center;
                    align-items: center;
                    h6{
                        color: black;
                        font-weight: 400;
                        @media (max-width:500px){
                            font-size: 15px;
                        }
                    }
                }
            }
            .abba{
                transform: scale(0.7);
                @media (max-width:500px){
                    width: 25%;
                }
            }
        }
    }
    .footer-img{
        img{
            width: 100%;
        }
    }
    .footer-nav{
        display: flex;
        @media (max-width:780px){
            display: none;
        }
        img{
            // border: solid 1px;
            max-width: 90%;
        }
        li{
            list-style: none;
            .nav-link{
                color: black;
                font-weight: 600;
                @media (max-width:990px){
                    font-size: 14px;
                }
                @media (max-width:830px){
                    font-size: 12px;
                }
            }
            .fa-phone{
                margin-right: -20px;
                cursor: pointer;
                color: rgb(0, 68, 68);
                padding: 10px 12px;
                font-size: 25px;
                border: solid 1px rgb(0, 68, 68);
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                @media (max-width:990px){
                    font-size: 14px;
                    padding: 6px 8px;
                }
                @media (max-width:830px){
                    font-size: 12px;
                }
                @media (max-width:780px){
                    font-size: 25px;
                }
                @media (max-width:600px){
                    font-size: 18px;
                    padding: 6px 8px;
                    margin-top: 5px;
                    display: none;
                }                
            }
            .numb{
                color: black;
                @media (max-width:990px){
                    font-size: 14px;
                }
                @media (max-width:830px){
                    font-size: 12px;
                }
            }
        }
    }
    .footer-nav1{
        display: block;
        align-items: center;
        justify-content: center;
        // border: solid 1px;        
        padding: 0;
        @media (min-width:780.1px){
            display: none;
        }
        li{
            list-style: none;
            .nav-link{
                color: black;
                font-weight: 600;               
            }
            .fa-phone{
                margin-right: -20px;
                cursor: pointer;
                color: rgb(0, 68, 68);
                padding: 10px 12px;
                font-size: 25px;
                border: solid 1px rgb(0, 68, 68);
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
            }
            .numb{
                color: black;               
            }
        }
        .w-1000{
            // border: solid 1px;
            width: 100%;
            @media (max-width:420px){
                min-width: 100%;
            }
        }
        .abba{
            transform: scale(0.5);
        }
    }
}