.Spinner{
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding: 0;
    margin: 0;
    background-color: #21091F;
    // border: solid 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    @media (max-width:500px){
        min-height: 80vh;
    }
    .wrapper{
        // padding: 100px 150px;
        display: flex;
        align-items: center;
        justify-content: center;        
        .cyrcle{
            cursor: pointer;
            position: absolute;                   
            width: 300px;
            height: 300px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: 0.3s;
            border-radius: 50%;
            z-index: 2;            
            // overflow: hidden; 
            @media (max-width:1600px) {
                width: 200px;
                height: 200px;
            }
            @media (max-width:500px) {
                width: 100px;
                height: 100px;
            }                     
            img{
                position: absolute;
                width: 100%;                
            }
            &:active{
                transform: scale(0.9);
            }
            audio{
                position: absolute;
                z-index: 100;
            }
        }
        .cyrcle.activing{
            transition: 0.3s;
            transform: scale(0.9);
        }
        .touch::before{
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            // z-index: -1;
            border: solid 20px rgba(255, 255, 255, 0.692);
            // transform: scale(0.9);
            animation-name: circleanim;
            opacity: 0;
            animation-duration: 1.5s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;                
        }
        .touch::after{
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            // z-index: -1;
            border: solid 20px rgba(255, 255, 255, 0.692);
            // transform: scale(0.9);
            animation-name: circleanim1;
            opacity: 0;
            animation-duration: 1.5s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;   
            animation-delay: 0.4s             
        }
        .text-border{
            // border: solid 1px white;
            position: absolute;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            width: 450px;
            height: 450px;
            z-index: 1;
            // display: none;
            @media (max-width:1600px){
                width: 300px;
                height: 300px;
            }
            @media (max-width:500px){
                width: 200px;
                height: 200px;
            }
            p{
                color: rgb(102, 102, 102);      
                font-size: 20px; 
                font-weight: 1000;
                position: absolute;    
                @media (max-width:500px){
                    font-size: 16px; 
                    font-weight: 700;
                }        
                span{
                    position: absolute;
                }
                span.activating{
                    color: white;
                    transition: 0.3s;                                      
                }                
            }
        }
        .canvas-main{
            background-color: #21091F; 
            transform-origin: center;
            transition: 70s linear;
            width: 100%;
            height: 100%;
        }
        canvas{
            background-color: #21091F; 
            transform-origin: center;
            transition: 70s linear;
        }
        canvas.scale{
            background-color: #21091F; 
            transform-origin: center;
            transition: 70s linear;
            animation-name: canvas;
            animation-iteration-count: infinite;
            animation-duration: 2s;
            animation-timing-function: linear;
            // color: rgba(255, 255, 255, 0.849);
        }
        .canvas-num{
            background-color: transparent;
            position: absolute;
            z-index: 10001;
        }
        .number{
            color: rgb(255, 255, 255);
            font-size: 50px;
            position: absolute;
            z-index: 1001;
            // right: 0;
            margin-left: 25vw;
            font-weight: 700;
            animation-name: number;
            opacity: 0;
            animation-duration: 6.5s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
            -webkit-text-stroke: 1px #021A38;
            font-family: arial;
            @media (max-width:1500px){
                font-size: 45px;
            }
            @media (max-width:1100px){
                font-size: 40px;
                margin-left: 30vw;
            }
            @media (max-width:900px){
                font-size: 35px;
                font-weight: 600;
                margin-left: 35vw;
            }
            @media (max-width:500px){
                font-size: 30px;
                font-weight: 500;
                margin-left: 45vw;
                animation-duration: 4s;
                animation-name: number1;
            }
            @media (max-width:400px){
                font-size: 27px;
                font-weight: 500;
                margin-left: 50vw;
            }
        }
        
    }
}

@keyframes circleanim {
    0%{
        transform: scale(0.5);
    }
    50%{
        opacity: 1;
    }
    100%{
        transform: scale(1.5);
    }
}

@keyframes circleanim1 {
    0%{
        transform: scale(0.5);
    }
    50%{
        opacity: 1;
    }
    100%{
        transform: scale(1.5);
    }
}

@keyframes canvas {
    0%{
        transform: scale(1);
    }
    25%{
        transform: scale(1.05);
    }
    50%{
        transform: scale(1);
    }
    75%{
        transform: scale(0.95);
    }
    100%{
        transform: scale(1);
    }
}

@keyframes number {
    0%{
        opacity: 0;
        transform: translateY(0%);
    }
    15%{
        opacity: 1;
        transform: translateY(-100%);
    }
    31%{
        transform: translateY(-200%);
        opacity: 0;
    }
    100%{
        opacity: 0;
        transform: translateY(0%);
    }
}

@keyframes number1 {
    0%{
        opacity: 0;
        transform: translateY(0%);
    }
    25%{
        opacity: 1;
        transform: translateY(-100%);
    }
    50%{
        transform: translateY(-200%);
        opacity: 0;
    }
    100%{
        opacity: 0;
        transform: translateY(0%);
    }
}