.Header{
    position: relative;
    padding: 0;
    margin: 0;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    @media (max-width:767px){
        height: 120%;
    }
    @media (max-width:500px){
        height: 110%;
    }
    @media (max-width:450px){
        height: 100%;
    }
    .wrapper{
        padding: 230px 10vw;
        @media (max-width:1629px){
            padding: 200px 5vw;
        }
        @media (max-width:1555px){
            padding:200px 50px;
        }
        @media (max-width:1250px){
            padding: 200px 80px;
        }
        @media (max-width:1000px){
            padding: 200px 60px;
        }
        @media (max-width:870px){
            padding: 200px 30px;
        }
        @media (max-width:767px){
            padding: 150px 150px;
        }
        @media (max-width:700px){
            padding: 150px 100px;
        }
        @media (max-width:560px){
            padding: 150px 50px;
        }
        .row{
            // border: solid 1px;
            padding: 0 10%;
            @media (max-width:1500px){
                padding: 0 10%;
            }
            @media (max-width:1349px){
                padding: 0 10%;
            }
            @media (max-width:767px){
                padding: 0;
            }            
            @media (max-width:360px){
                padding: 0;
            }
            .header-text{
                // border: solid 1px;
                padding-top: 5%;
                .favorite{
                    text-align: left;
                    font-family: 'Fredoka', sans-serif;
                    font-size: 80px;
                    font-weight: 600;
                    background: linear-gradient(226.42deg, #0F5F4B 8.93%, #09152F 110.98%);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    background-clip: text;
                    @media (max-width:1500px){
                        font-size: 70px;
                    }
                    @media (max-width:1349px){
                        font-size: 60px;
                    }
                    @media (max-width:1210px){
                        font-size: 50px;
                    }
                    @media (max-width:1035px){
                        font-size: 45px;
                    }
                    @media (max-width:905px){
                        font-size: 40px;
                    }
                    @media (max-width:767px){
                        font-size: 45px;
                    }
                    @media (max-width:440px){
                        font-size: 40px;
                    }
                    @media (max-width:400px){
                        font-size: 38px;
                    }
                    @media (max-width:385px){
                        font-size: 35px;
                    }
                    @media (max-width:345px){
                        font-size: 33px;
                    }
                    @media (max-width:330px){
                        font-size: 30px;
                    }
                }
                p{
                    margin-top: 20px;
                    font-family: 'Helvetica';
                    font-style: italic;
                    font-weight: 300;
                    font-size: 20px;
                    // line-height: 93.7%;
                    background: linear-gradient(226.42deg, #0F5F4B 8.93%, #09152F 110.98%);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    background-clip: text;
                    @media (max-width:810px){
                        font-size: 18px;
                    }
                }
                .btn{
                    margin-top: 20px;
                    padding: 7px 25px;
                    font-family: 'Fredoka', sans-serif;
                    color: black;
                    background: linear-gradient(135deg, #FBDA61 2.88%, #F76B1C 98.13%);
                    border: 2.29598px solid #000000;
                    border-radius: 30px;
                    font-weight: 500;
                    font-size: 18px;
                    transition: 0.3s;
                    .nav-link{
                        color: black;
                    }
                    &:hover{
                        transform: scale(1.05);
                    }
                    &:active{
                        transform: scale(0.95);
                    }
                    @media (max-width:810px){
                        padding: 3 15px;
                        font-size: 16px;
                    }
                }
            }

            .header-img{
                // border: solid 1px;
                position: relative;
                padding: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                .back{
                    // border: solid 1px;                    
                    width: 100%;
                    position: absolute;
                    // height: 100%;
                    animation-name: animback;
                    animation-duration: 25s;
                    animation-timing-function: linear;
                    animation-iteration-count: infinite;
                }
                .prod{
                    position: absolute;                                                                    
                    z-index: 2;                                                
                    width: 110%;  
                    height: 130%;
                }
                @media (max-width:767px){
                    display: none;
                }
            }
            .header-img-top{
                // border: solid 1px;
                position: relative;
                padding: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                overflow: hidden;
                .back{
                    // border: solid 1px;                    
                    width: 100%;
                    // position: absolute;
                    // height: 100%;
                    animation-name: animback;
                    animation-duration: 25s;
                    animation-timing-function: linear;
                    animation-iteration-count: infinite;
                }
                .prod{
                    position: absolute;                                                                    
                    z-index: 2;                                                
                    width: 105%;  
                    height: 105%;
                }
                @media (min-width:767.1px){
                    display: none;
                }
            }
        }
    }
    .flower{
        // border: solid 1px;
        position: absolute;
        left: -15%;
        overflow: hidden;  
        z-index: -1;          
        @media (max-width:767px){
            display: none;
        }
        img{
            width: 100%;
            height: 100%;
            animation-name: animflower;
            animation-duration: 30s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
        }
    }
    .two-flower{
        // border: solid 1px;
        position: absolute;
        right: -5%;
        overflow: hidden;  
        z-index: -1;    
        // height: 70%;
        // width: 25%;
        transform: scaleY(1.3) rotate(4deg);      
        @media (max-width:767px){
            display: none;
        }
        img{
            width: 100%;
            height: 100%;
            animation-name: animtwoflower;
            animation-duration: 25s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
        }
    }
    .bg-creem{
        padding: 0;
        margin: 0;
        position: absolute;
        right: 0;
        top: 0;
        width: 30%;
        height: 100%;
        z-index: -1;
        background-color: #F9F4EA;
        @media (max-width:767px){
            display: none;
        }
    }
}

@keyframes animback {
    100%{
        transform: rotate(360deg);
    }
}
@keyframes animflower {
    100%{
        transform: rotate(-360deg);
    }
}