.Blog{
    padding: 0;
    margin: 0;
    .wrapper{
        padding: 100px 150px;
        @media (max-width:1250px){
            padding: 100px;
        }
        @media (max-width:1000px){
            padding: 100px 60px;
        }
        @media (max-width:870px){
            padding: 100px 30px;
        }
        @media (max-width:500px){
            padding: 40px;
        }
        @media (max-width:440px){
            padding: 20px;
        }
        .blog{
            color: darkcyan;
        }
        .usefull{
            font-family: 'Fredoka', sans-serif; 
            padding-left: 15px;            
            font-weight: 500;
            font-size: 55px;
            @media (max-width:1460px){
                font-size: 50px;
            }
            @media (max-width:1350px){
                font-size: 45px;
            }
            @media (max-width:1250px){
                font-size: 40px;
            }
            @media (max-width:1150px){
                font-size: 35px;
            }
            @media (max-width:815px){
                font-size: 30px;
            }
            @media (max-width:767px){
                font-size: 40px;
            }
        }
        .view-all{
            margin-top: 10px;
            color: black;
            border: solid 1px black;
            padding: 5px 20px;
            text-decoration: none;
            font-weight: 600;
            border-radius: 10px;
            -webkit-border-radius: 10px;
            -moz-border-radius: 10px;
            -ms-border-radius: 10px;
            -o-border-radius: 10px;
            transition: 0.3s;
            -webkit-transition: 0.3s;
            -moz-transition: 0.3s;
            -ms-transition: 0.3s;
            -o-transition: 0.3s;
            .nav-link{
                color: black;                
            }
            &:hover{
                background-color: black;
                .nav-link{
                    color: white;
                }
            }
            @media (max-width:500px){
                font-size: 12px;
                padding: 5px 0;
            }
            @media (max-width:450px){
                margin-top: 0;
            }
            @media (max-width:400px){
                font-size: 12px;
                padding: 0;
            }
        }
        .white-all{
            margin-top: 10px;
            color: white;
            border: solid 1px white;
            padding: 5px 20px;
            text-decoration: none;
            font-weight: 600;
            border-radius: 10px;
            -webkit-border-radius: 10px;
            -moz-border-radius: 10px;
            -ms-border-radius: 10px;
            -o-border-radius: 10px;
            transition: 0.3s;
            -webkit-transition: 0.3s;
            -moz-transition: 0.3s;
            -ms-transition: 0.3s;
            -o-transition: 0.3s;
            .nav-link{
                color: white;                
            }
            &:hover{
                background-color: white;
                .nav-link{
                    color: black;
                }
            }
            @media (max-width:500px){
                font-size: 12px;
                padding: 5px 0;
            }
            @media (max-width:400px){
                font-size: 12px;
                padding: 0;
            }
        }
        .slider{
            // display: grid;
            align-items: center;
            padding-bottom: 20px;   
            @media (max-width:500px){
                padding-bottom: 0;
            }
            .slider-carousel{
                @media (max-width:767px){
                    display: none;
                }  
            }
            .slider-carousel-0{
                @media (min-width:767.1px){
                    display: none;
                }  
            }
            .blog-img{
                border-radius: 15px;
            }
            .col-md-4{
                padding: 15px;
                border: none;  
                @media (max-width:500px){
                    padding: 5px;
                }
                @media (max-width:450px){
                    margin-top: 60px;
                }              
                img{
                    width: 100%;
                    height: 40vh;
                    @media (max-width:500px){
                        height: 18vh;                        
                    }
                    @media (max-width:450px){
                        height: 26vh;                        
                    }
                    @media (max-width:340px){
                        height: 24vh;                        
                    }    
                    @media (max-width:300px){
                        height: 21vh;                        
                    }            
                }
                h5{
                    margin-top: 40px;
                    height: 50px;
                    overflow: hidden;
                    @media (max-width:500px){
                        font-size: 14px;
                        margin-top: 20px;
                    }
                }
                .fa-arrow-right{
                    margin-left: 20px;
                    @media (max-width:500px){
                        font-size: 10px; 
                        margin-left: 5px;                      
                    }
                }
            }
            .slick-next{          
                right: 100px;       
                top: 570px; 
                @media (max-width:500px){
                    top: 300px; 
                    right: 10px; 
                }
            }
            .slick-next:before{
                content: "\f105";
                font-family: FontAwesome;
                font-style: normal;   
                font-weight: 100;  
                color: rgb(56, 56, 56);                
                font-size: 35px;
                font-weight: 900; 
                @media (max-width:500px){
                    font-size: 28px;
                }       
            }
            .slick-prev{       
                left: 70%;
                top: 570px;
                @media (max-width:500px){
                    top: 300px; 
                    left: 70%; 
                }
            }
            .slick-prev:before{
                content: "\f104";
                font-family: FontAwesome;
                font-style: normal;   
                font-weight: 100;  
                color: rgb(56, 56, 56);
                font-size: 35px;
                font-weight: 900;  
                @media (max-width:500px){
                    font-size: 28px;
                }        
            }
        }       
    }
}
.img-left, .img-right, .img-middle{
    position: absolute;
    width: 100%;
    height: 100%;
}

.bg-darkcyan{
    background-color: rgb(0, 34, 53);
}