.Product{
    padding: 0;
    margin: 0;
    border: 0;
    .wrapper{
        padding: 250px 150px 20px 150px;
        @media (max-width:1250px){
            padding: 250px 100px 20px 100px;
        }
        @media (max-width:1000px){
            padding: 250px 60px 20px 60px;
        }
        @media (max-width:870px){
            padding: 250px 30px 20px 30px;
        }
        @media (max-width:500px){
            padding: 170px 40px 20px 40px;
        }
        @media (max-width:440px){
            padding: 170px 20px 20px 20px;
        }
        .check{
            text-align: center;
            margin-bottom: 30px;
            @media (max-width:540px){
                text-align: left;
                padding-left: 20px;
                margin-bottom: 10px;
            }
            h1{
                font-family: 'Fredoka', sans-serif;        
                background: linear-gradient(226.42deg, #0F5F4B 8.93%, #09152F 110.98%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                background-clip: text;
                font-size: 55px;
                font-weight: 500;
                @media (max-width:540px){
                    font-size:50px;
                }
                @media (max-width:500px){
                    font-size: 45px;
                }
                @media (max-width:450px){
                    font-size: 40px;
                }
                @media (max-width:400px){
                    font-size: 37px;
                }                
                @media (max-width:345px){
                    font-size: 35px;
                }
                @media (max-width:330px){
                    font-size: 33px;
                }
            }
        }
        .prod-0{
            @media (max-width:767px){
                display: none;
            }
            .prod{
                // border: solid 1px;                
                padding: 30px;
                margin-top: 20px;
                display: flex;
                .list{   
                    // border: solid 1px;                                                  
                    width: 100%;    
                    text-decoration: none;
                    text-align: center;
                    cursor: pointer;                                     
                    animation-timing-function: linear;                    
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                    .list-img{
                        position: relative;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        min-width: 100%;                                                    
                    }      
                    .orange{
                        width: 100%;
                        height: 100%;
                    }              
                    .out{                         
                        position: absolute;                                                                    
                        z-index: 2;                                                
                        width: 70%;  
                        height: 70%;                                                                                              
                    }
                    .flour{
                        margin-top: 40px;
                        text-align: center;
                        font-family: 'Helvetica';
                        font-style: italic;
                        font-weight: 300;
                    }  
                    .info{
                        text-align: center;
                        font-family: 'Helvetica';
                        font-weight: 600;
                    }                                       
                    &:hover{
                        animation-name: anime1;
                    }
                }
            }
            
            .button{
                text-align: center;
                .see-all{
                    border-left: solid 1px black;
                    border-bottom: solid 1px black;                
                    background-color: darkcyan;
                    margin-top: 40px;
                    color: white;
                    padding:5px 60px;
                    text-decoration: none;
                    font-weight: 600;
                    border-radius: 15px;
                    -webkit-border-radius: 15px;
                    -moz-border-radius: 15px;
                    -ms-border-radius: 15px;
                    -o-border-radius: 15px;
                    transition: 0.3s;
                    -webkit-transition: 0.3s;
                    -moz-transition: 0.3s;
                    -ms-transition: 0.3s;
                    -o-transition: 0.3s;
                    .nav-link{
                        color: white;
                    }
                    &:hover{
                        background-color: rgb(0, 184, 184);
                        .nav-link{
                            color: black;
                        }
                    }
                }    
            }
        }
        .prod-1{
            @media (min-width:767.1px){
                display: none;
            }
            .prod{
                padding: 30px;
                margin-top: 20px;
                display: flex; 
                @media (max-width:650px){
                    padding: 30px 15px;
                }  
                @media (max-width:370px){
                    padding: 30px 10px;
                }
                @media (max-width:330px){
                    padding: 30px 5px;
                }                 
                .list{
                    text-decoration: none;
                    text-align: center;
                    cursor: pointer;                   
                    animation-duration: 1.4s;
                    animation-timing-function: linear;                    
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                    @media (max-width:330px){
                        padding: 5px;
                        border-radius: 7px;
                        -webkit-border-radius: 7px;
                        -moz-border-radius: 7px;
                        -ms-border-radius: 7px;
                        -o-border-radius: 7px;
                    }
                    .list-img{
                        position: relative;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        min-width: 100%;      
                    }
                    .orange{
                        width: 100%;
                        height: 100%;
                    }              
                    .out{                         
                        position: absolute;                                                                    
                        z-index: 2;                                                
                        width: 70%;  
                        height: 70%;                                                                                              
                    }
                    .flour{
                        margin-top: 40px;
                        text-align: center;
                        font-family: 'Helvetica';
                        font-style: italic;
                        font-weight: 300;
                    }  
                    .info{
                        text-align: center;
                        font-family: 'Helvetica';
                        font-weight: 600;
                    }                                          
                    &:hover{
                        animation-name: anime1;
                    }
                }
            }           
        }        
    }
    .header-images{
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        overflow: hidden;
        .flower{
            // border: solid 1px;
            top: 10%;
            position: absolute;
            left: -15%;
            overflow: hidden;  
            z-index: -1;          
            @media (max-width:767px){
                display: none;
            }
            img{
                width: 100%;
                height: 100%;
                animation-name: animflower;
                animation-duration: 30s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
            }
        }
        .two-flower{
            // border: solid 1px;
            position: absolute;
            right: -5%;
            overflow: hidden;  
            z-index: -1;    
            top: 5%;
            transform: scaleY(1.3) rotate(4deg);      
            @media (max-width:767px){
                display: none;
            }
            img{
                width: 100%;
                height: 100%;
                animation-name: animtwoflower;
                animation-duration: 25s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
            }
        }
        .bg-creem{
            padding: 0;
            margin: 0;
            position: absolute;
            right: 0;
            top: 0;
            width: 30%;
            height: 100%;
            z-index: -1;
            background-color: #F9F4EA;
            @media (max-width:767px){
                display: none;
            }
        }
    }
}

@keyframes anime1 {
    25%{
        transform: translateX(10px);
        -webkit-transform: translateX(10px);
        -moz-transform: translateX(10px);
        -ms-transform: translateX(10px);
        -o-transform: translateX(10px);
}
    50%{
        transform: translateX(0px);
        -webkit-transform: translateX(0px);
        -moz-transform: translateX(0px);
        -ms-transform: translateX(0px);
        -o-transform: translateX(0px);
}
    75%{
        transform: translateX(-10px);
        -webkit-transform: translateX(-10px);
        -moz-transform: translateX(-10px);
        -ms-transform: translateX(-10px);
        -o-transform: translateX(-10px);
}    
}

@keyframes animback {
    100%{
        transform: rotate(360deg);
    }
}
@keyframes animflower {
    100%{
        transform: rotate(-360deg);
    }
}

.bg-darkcyan{
    background-color: rgb(0, 34, 53);
}
.green{
    background: url('../images/green.png') no-repeat;
    background-size: 100% 100%;
}
.blue{
    background: url('../images/blue.png') no-repeat;
    background-size: 100% 100%;
}