.Contacts{
    padding: 0;
    margin: 0;
    .wrapper{
        padding: 150px;
        @media (max-width:1300px){
            padding: 150px 30px;
        }        
        .row{            
            .message{
                padding: 20px 150px;
                position: relative;   
                @media (max-width:1680px){
                    padding: 20px 8vw;
                }
                @media (max-width:1400px){
                    padding: 20px 4vw;
                }             
                .fa-mobile{
                    color: rgb(54, 54, 54);
                    font-size: 30px;
                    position: absolute;
                    top: 50px;
                    right: 30px;                    
                }
                .fa-user-o{
                    color: rgb(58, 58, 58);
                    font-size: 25px;
                    position: absolute;
                    top: 50px;
                    right: 20px;                                       
                }
                .form-control{
                    position: relative;
                    color: white;
                    padding: 15px;
                    padding-right: 45px;
                    border-radius: 10px;
                    font-weight: 600;
                    background-color: #EC691F;
                    -webkit-border-radius: 10px;
                    -moz-border-radius: 10px;
                    -ms-border-radius: 10px;
                    -o-border-radius: 10px;
                    @media (max-width:500px){
                        max-width: 100vw;
                    }
                    &:focus{
                        border: solid 1px rgb(84, 0, 117);
                        box-shadow: none;
                        .fa-user-o, .fa-mobile{
                            color: darkcyan;
                        }
                    }
                }
                .red-line{
                    border: solid 2px red;
                    background-color: #fccaae;
                }
                .tel-control{                    
                    min-width: 19vw;                                    
                    padding-left: 50px;
                    @media (max-width:1349px){
                        min-width: 17vw;  
                    }
                    @media (max-width:1270px){
                        min-width: 15vw;  
                    }
                    @media (max-width:500px){
                        padding-right: 160px;
                    }
                }
                label{
                    font-weight: 600;                                        
                    background: -webkit-linear-gradient(-45deg, crimson, rgb(84, 0, 117));
                    background-clip: text;
                    -webkit-text-fill-color: transparent;
                }
                .any{
                    font-family: 'Fredoka', sans-serif; 
                    margin-bottom: 50px;
                    text-align: center;
                    font-size: 50px;
                    font-weight: 500;                                        
                    background: -webkit-linear-gradient(-45deg, crimson, rgb(84, 0, 117));
                    background-clip: text;
                    -webkit-text-fill-color: transparent;
                    @media (max-width:440px){
                        font-size: 40px;
                    }
                    @media (max-width:365px){
                        font-size: 35px;
                    }
                    @media (max-width:330px){
                        font-size: 30px;
                    }
                }
                .btn{
                    padding: 15px 40px;
                    margin-top: 30px;
                    background: -webkit-linear-gradient(-45deg, crimson, rgb(84, 0, 117));
                    border-radius: 15px;
                    color: white;
                    font-size: 18px;
                    font-weight: 600;
                    transition: 0.4s;
                    &:hover{
                        transform: scale(1.05);
                    }
                    &:active{
                        transform: scale(0.95);
                    }
                }
            }
        }
    }
}