.Category{
    padding: 0;
    margin: 0;
    .wrapper{
        padding: 100px 150px;
        @media (max-width:1250px){
            padding: 100px;
        }
        @media (max-width:1000px){
            padding: 100px 60px;
        }
        @media (max-width:870px){
            padding: 100px 30px;
        }
        @media (max-width:500px){
            padding: 40px;
        }
        @media (max-width:440px){
            padding: 20px;
        }
        .other{            
            text-align: center;
            @media (max-width:600px){
                text-align: left;
                padding-left: 20px;
            }
            h1{
                font-family: 'Fredoka', sans-serif; 
                background: linear-gradient(226.42deg, #0F5F4B 8.93%, #09152F 110.98%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                background-clip: text;
                font-size: 65px;
                font-weight: 500;
                @media (max-width:600px){
                    font-size: 55px;
                }
                @media (max-width:540px){
                    font-size:50px;
                }
                @media (max-width:500px){
                    font-size: 45px;
                }
                @media (max-width:450px){
                    font-size: 40px;
                }
                @media (max-width:400px){
                    font-size: 37px;
                }                
                @media (max-width:345px){
                    font-size: 35px;
                }
                @media (max-width:330px){
                    font-size: 33px;
                }
            }
        }
        .row{
            margin-top: 20px;
            .col-md-6{
                padding: 20px;
                .nav-link{
                    position: relative;
                    padding: 0;               
                    height: 35vh;
                    border-radius: 15px;
                    overflow: hidden;
                    @media (max-width:500px){
                        height: 30vh;
                    }
                    @media (max-width:430px){
                        height: 25vh;
                    }
                    @media (max-width:390px){
                        height: 20vh;
                    }
                    img{                        
                        width: 100%;                        
                        height: 100%;
                    }
                    h5{
                        color: black;
                        font-weight: 600;
                        top: 30px;
                        left: 20px;
                        position: absolute;
                    }
                }
            }
        }
    }
}